* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  perspective: 300px;
  min-height: 100dvh;
  color: var(--text-c);
  background: #91d2ca;
  font-family: Sora, sans-serif;
  font-weight: 300;
  overflow: hidden;
}

html [lang] {
  display: none;
}

html[lang="ge"] [lang="ge"], html[lang="en"] [lang="en"] {
  display: unset;
}

body {
  width: 100vw;
  height: 100dvh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  overflow: hidden;
}

a, a:visited {
  color: inherit;
  text-decoration: none;
}

[hidden] {
  display: none;
}

#canvas-0 {
  max-width: 100%;
  max-height: 40vh;
}

.u-flex-space {
  flex-grow: 9999;
}

#root {
  transform-origin: 50%;
}

.Main {
  --gap: 32px;
  justify-content: flex-start;
  align-items: center;
  gap: var(--gap);
  width: 100%;
  height: var(--main-h);
  margin-top: var(--topbar-dh);
  overflow-x: hidden;
  overflow-y: overlay;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
  flex-direction: column;
  display: flex;
}

.Main section {
  --pad: 24px;
  clear: both;
  width: 100%;
  max-width: 556px;
  min-height: calc(var(--main-h)  - var(--gap)  - var(--pad));
  scroll-snap-align: start;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  gap: 32px;
  padding-bottom: 24px;
  display: flex;
}

.Main section#intro {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
  display: flex;
}

.Main p a, .Main p a:visited {
  color: #a57637;
}

.Main__socs {
  --w: 256px;
  --mar-t: 30px;
  width: 100%;
  max-width: var(--w);
  margin-top: var(--mar-t);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.Main__socs a {
  --s: 32px;
  width: var(--s);
  height: var(--s);
}

.Main__socs a img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.Root__bg, .Root__bg img {
  width: 100%;
  max-width: 100vw;
  height: 100%;
  z-index: -1;
  object-fit: cover;
  position: absolute;
}

.Root__shadow {
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: linear-gradient(100deg, #fff0 19%, #fff 51%, #fff0 96%), linear-gradient(-5deg, #fff0 59%, #fff 90%, #fff0 100%);
  position: absolute;
}

#debug {
  width: 100%;
  white-space: break-spaces;
  background: #fff;
  padding: 4px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

#gmap {
  width: 100%;
  height: 100%;
  max-height: 50vh;
  min-height: initial;
  max-width: initial;
}

.Intro__logo {
  max-width: 100%;
  max-height: 40vh;
}

.warning {
  color: #4b5aae;
  -webkit-backdrop-filter: blur(18.5px);
  backdrop-filter: blur(18.5px);
  background: #4b5aae1a;
  border-radius: 24px;
  flex-direction: row;
  align-items: flex-start;
  gap: 10px;
  padding: 16px;
  display: flex;
  box-shadow: inset 0 0 4px #4b5aae33;
}

.Tickets__cont {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

@media (min-width: 741px) {
  #info .Tickets__cont {
    flex-direction: row;
  }
}

.TicketsBtn {
  min-width: 174px;
  letter-spacing: .001em;
  color: var(--text-c);
  border: 1px solid var(--text-c);
  justify-content: center;
  align-items: center;
  padding: 16px 24px;
  font-size: 20px;
  text-decoration: none;
  display: inline-flex;
}

.Btn, button {
  font-weight: 300;
}

@media (min-width: 741px) {
  html {
    font-size: 20px;
  }

  .d-sm {
    display: none !important;
  }

  section#intro, section#info, section#team {
    text-align: center;
  }
}

@media (max-width: 740px) {
  html {
    font-size: 16px;
  }

  .d-not-sm {
    display: none !important;
  }

  .Main > section {
    align-items: flex-start;
    padding: 0 24px;
  }

  .Main__socs {
    --w: 152px;
  }

  .Main__socs a {
    --s: 24px;
  }

  .TicketsBtn {
    font-size: 14px;
  }

  .Root__shadow {
    background-image: linear-gradient(80deg, #fff0 0%, #fff 31% 51%, #fff0 100%), linear-gradient(-5deg, #fff0 59%, #fff 90%, #fff0 100%);
  }
}

